<template>
	<div class="publish">
		<div class="pubHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{pubName}}</p>
			<div class="annouce">
				<div @click="publishing()" class="selectStu">{{pubBtn}}</div>
			</div>
		</div>
		<hr>
		<div class="editor">
			<div class="title">
				<p class="intro"></p>
				<input v-model="title" type="text" name="search" placeholder=""  autocomplete="off">
				<div class="TBtn" v-if="name == 'test' || name == 'hw' || name == 'Retest' || name == 'Rehw' ? true:false">
					<span @click="dialogVisible = true"></span>
					<span>{{startTime}}</span>-<span>{{endTime}}</span>
				</div>
			</div>
			<div class="contain">
				<p class="intro"></p>
				<div class="rest">
					<Edit @newsEvent="showInfo" :Tcontent="content" :key="old.infoID"></Edit>
				</div>
			</div>
		</div>
		<el-dialog
		  title=""
		  :visible.sync="dialogVisible"
		  width="50%">
		  <div class="block">
			    <el-date-picker
			      v-model="deadline"
			      type="datetimerange"
			      align="right"
			      start-placeholder=""
			      end-placeholder=""
			      :default-time="['12:00:00', '08:00:00']">
			    </el-date-picker>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false"> </el-button>
		    <el-button type="primary" @click="getDeadline"> </el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import Edit from '../components/ue.vue';
export default {
	name: 'publish',
	components:{
		Edit
	},
	data(){
		return {
			param: new FormData(),
			courseID: '',
			deadline: '',
			pubName: '',
			pubBtn: '',
			title: '',
			content: '',
			name: '',
			startTime: '',
			endTime: '',
			dialogVisible: false,
			url: '',
			old:{
				title: '',
				content: '',
				publisher: '',
				infoID: '',
				startTime: '',
				endTime: ''
			}
		}
	},
	mounted(){
		if (this.getToken()) {
			// this.param.
			var ifRe = this.$route.query.name.indexOf('Re');
			ifRe == 0 ? this.correctInfo():this.pubGetName();
		}
	},
	methods:{
		//
		pubGetName(){
			var query = this.$route.query;
			this.name = query.name;
			this.pubBtn = '';
			this.courseID = sessionStorage.getItem('courseID');
			this.param.append('token',this.getToken());
			switch(this.name) {
				case 'news':
					this.pubName = '';
					this.url = this.$baseUrl + '/aNews';
					break;
				case 'inform':
					this.pubName = '';
					this.param.append('courseId',this.courseID);
					this.url = this.$baseUrl + '/pNotice';
					break;
				case 'test':
					this.pubName = '';
					this.param.append('courseId',this.courseID);
					this.url = this.$baseUrl + '/homework/pub';
					break;
				case 'hw':
					this.pubName = '';
					this.param.append('courseId',this.courseID);
					this.url = this.$baseUrl + '/homework/pub';
					break;
				default :
					break;
			}
		},
		//
		correctInfo(){
			var that = this;
			var query = this.$route.query;
			var OldParam = new FormData();
			OldParam.append('id',query.id);
			OldParam.append('token',this.getToken());
			this.name = query.name;
			console.log(query.name)
			var oldUrl = '';
			this.pubBtn = '';
			switch(this.name){
				case 'Renews':
					this.pubName = '';
					this.url = this.$baseUrl + '/uNews';
					oldUrl = this.$baseUrl + '/news/' + query.id;
					break;
				case 'Retest':
					this.pubName = '';
					this.url = this.$baseUrl + '/homework/update';
					oldUrl = this.$baseUrl + '/homework/' + query.id;
					break;
				case 'Rehw':
					this.pubName = '';
					this.url = this.$baseUrl + '/homework/update';
					oldUrl = this.$baseUrl + '/homework/' + query.id;
					break;
				default :
					break;
			}
			//
			// this.axios.post(oldUrl,query.theParam)
			this.axios.post(oldUrl,OldParam)
			.then((res)=>{
				var that = this;
				// console.log(res.data);
				if (res.data.code == 200) {
					let data = res.data.data;
					that.old.title = data.title;
					that.old.content = data.content;
					that.old.publisher = data.publisherId;
					that.old.infoID = data.id;
					if (this.name == 'Retest' || this.name == 'Rehw') {
						that.old.startTime = data.startTime;
						that.old.endTime = data.endTime;
						that.startTime = data.startTime;
						that.endTime = data.endTime;
					}
					//
					that.title = data.title;
					that.content = data.content;
				} else {
					this.$message.error('');
				}
			})
		},
		//
		publishing(){
			if (this.title == '') {
				this.$message.error('');
				return ;
			}
			if( this.content == '') {
				this.$message.error('');
				return ;
			}
			console.log(this.name)
			if (this.name == 'test' || this.name == 'hw') {
				if (this.startTime == '') {
					this.$message.error('');
					return ;
				} else {
					this.param.append('startTime',this.startTime);
					this.param.append('endTime',this.endTime);
					this.param.append('category',this.name);
				}
			}
			if (this.pubBtn == '') {
				if ((this.title == this.old.title) && (this.content == this.old.content)) {
					this.$message.error('');
				} else {
					this.corNews();
				}
			} else {
				this.pubNews();
			}
		},
		//
		pubNews(){
			var that = this;
			this.param.append('content',this.content);
			this.param.append('title',this.title);
			this.axios.post(this.url,this.param)
			.then((response)=>{
				var res = response.data;
				if (res.code == 200) {
					that.$message.success('');
					that.goBack();
				} else {
					that.$message.error('');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//
		corNews(){
			var that =this;
			this.param.append('content',this.content);
			this.param.append('title',this.title);
			this.param.append('token',this.getToken());
			this.param.append('publisherId',this.old.publisher);
			this.param.append('id',this.old.infoID);
			this.axios.post(this.url,this.param)
			.then((response)=>{
				var res = response.data;
				if (res.code == 200) {
					that.$message.success('');
					that.goBack();
				} else {
					that.$message.error('');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//
		getOldInfo(){
			var that =this;
			this.axios.post(this.$baseUrl + '/pNotice',{
				content: this.content,
				title: this.title,
			})
			.then((response)=>{
				var res = response.data;
				res.code == 200 ? that.$message.success(''):that.$message.error('');
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//
		getDeadline(){
			console.log(this.deadline);
			this.startTime = this.DateTranse(this.deadline[0]);
			this.endTime = this.DateTranse(this.deadline[1]);
			this.dialogVisible = false;
		},
		//
		DateTranse(data){
			var d = new Date(data);
			var hour = d.getHours() < 10 ? '0'+ d.getHours():d.getHours();
			var minu = d.getMinutes() < 10 ? '0'+ d.getMinutes():d.getMinutes();
			var sec = d.getSeconds() < 10 ? '0'+ d.getSeconds():d.getSeconds();
			var datetime=d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate() + ' ' + hour + ':' + minu + ':' + sec;
			return datetime;
		},
		//
		goBack() {
			this.$router.go(-1);
		},
		//
		showInfo(e){
			this.content = e;
		},
		//token
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		}
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.publish{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 0;
	}
	.pubHead{
		height: 65*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.annouce{
			margin-left: auto;
			display: flex;
			.selectStu{
				text-align: center;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				width: 100*@allwidth;
				color: #fff;
				background: #2b8290;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 20*@allwidth;
			}
		}
	}
	.editor{
		margin-top: 15*@allwidth;
		.title{
			display: flex;
			align-items: center;
			p{
				font-size: 22*@allwidth;
				width: 60*@allwidth;
				min-width: 35px;
				margin: 0 10px;
			}
			input{
				border: 1px solid #D2D2D2;
				width: 40%;
				height: 40*@allwidth;
				color: #666;
				line-height: 40*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				outline: none;
			}
			input:hover{
				border-color: #409EFF;
			}
			.TBtn{
				margin-left: 3%;
				display: flex;
				align-items: center;
				span{
					margin: 0 10*@allwidth;
				}
				span:first-child{
					color: #409EFF;
					line-height: 40*@allwidth;
					padding: 0 10*@allwidth;
					font-size: 18*@allwidth;
					background: #ecf5ff;
					border-radius: 10*@allwidth;
					cursor: pointer;
				}
				
			}
		}
		.contain{
			width: 100%;
			display: flex;
			margin-top: 10*@allwidth;
			.intro{
				font-size: 22*@allwidth;
				width: 60*@allwidth;
				margin: 0 10px;
			}
			.rest{
				width: 100%;
			}
		}
	}
}
.selfdialog{
	.el-dialog__body{
		padding-top: 0;
	}
}
</style>