<template>
  <div class="edit_container">
        <quill-editor 
            v-model="content" 
            ref="myQuillEditor" 
            :options="editorOption" 
            @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
        </quill-editor>
    </div>  
</template>

<script>
export default {
  name: 'edit_container',
  data(){
     return {
            content: '',
            editorOption: {
              modules: {
                toolbar:[
                  ['bold'],
                  ['image']
                ]
              }
            }
        }
  },
  computed: {
      editor() {
          return this.$refs.myQuillEditor.quill;
      },
  },
  mounted(){
    // console.log(this.Tcontent)
    this.content = this.Tcontent;
  },
  methods: {
    onEditorReady(editor) { // 
    },
    onEditorBlur(){
    }, // 
    onEditorFocus(){}, // 
    onEditorChange(){
      this.$emit('newsEvent',this.content);
    }, // 
  }
}
</script>

<style lang="less">
.edit_container{
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  .ql-toolbar.ql-snow{
    display: flex;
    padding: 2px 10px;
    .ql-formats{
      margin: 0;
    }
  }
}
.ql-editor{
   min-height: 120px;
}
</style>