<template>
	<div class="StuAcount">
		<div class="stuHead">
			<ul>
				<li>{{stuTitle}} </li>
			</ul>
			<div class="search">
				<input v-model="searches" type="text" name="search" placeholder="//" autocomplete="off">
				<i @click="Searching()" class="el-icon-search"></i>
			</div>
			<ul class="createAcount">
				<li>
					<el-upload
					  class="upload-demo"
					  :action="$baseUrl+'/batchImport'"
					  auto-upload
					  accept="*.xls"
					  :show-file-list="false"
					  :on-success="handleSuc"
					  :on-error="handleErr"
					  :data="excle"
					  >
					  <div style="color: #fff;"></div>
					</el-upload>
				</li>
				<li @click="appAcount()"></li>
			</ul>
		</div>
		<ul class="stuAcnt">
			<li>
				<el-row>
				  <el-col :span="5"></el-col>
				  <el-col :span="5"></el-col>
				  <el-col :span="5"></el-col>
				  <el-col :span="5"></el-col>
				  <el-col :span="4"></el-col>
				</el-row>
			</li>
			<li v-for="(item,index) in acntList" :key="index">
				<el-row>
				  <el-col :span="5">{{item.stuNum}}</el-col>
				  <el-col :span="5">{{item.name}}</el-col>
				  <el-col :span="5">{{item.sex}}</el-col>
				  <!-- <el-col :span="5">{{item.phone}}</el-col> -->
				  <el-col :span="5">{{item.phone}}</el-col>

				  <i @click="upAcount(index)" class="el-icon-edit-outline"></i>
		  		  <!-- <i @click="DelAcount(item.id)" class="el-icon-delete"></i> -->
				</el-row>
			</li>
		</ul>
		<div class="paging">
	  		<el-pagination
	  		  v-if='ispage'
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="10"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalNum">
		    </el-pagination>
	  	</div>
	  	<el-dialog :title="dialogTitle" width="40%" :visible.sync="dialogFormVisible">
	  		 <el-input v-model="form.name" autocomplete="off" placeholder="" oninput="value=value.replace(/[^\d]/g,'')"></el-input>
	  		 <el-input v-if="isUpPsd" v-model="form.psd" autocomplete="off" placeholder="" style="margin-top: 10px;"></el-input>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false"> </el-button>
		    <el-button type="primary" @click="simpleAcount()"> </el-button>
		  </div>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'StuAcount',
	data(){
		return {
			stuTitle: '',
			searching: '',
			activePage: 1,
			totalNum: 50,
			ispage: false,
			acntList:[],
      		param: new FormData(),
      		excle: {
      			token: ''
      		},
      		dialogTitle: '',
			dialogFormVisible: false,
			isUpPsd: false,
			form:{
				name: '',
				psd: ''
			},
			searches: '',
			formLabelWidth: '120px'
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addParamValue();
		}
	},
	methods:{
		//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//
			this.param.set('currentPage',this.activePage);
			this.getAcount();
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
			
		},
		//
		addParamValue(){
			//activePage
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('token',this.getToken());
			this.excle.token = this.getToken();
			this.param.append('currentPage',this.activePage);
			this.param.append('pageNum',10);
			this.getAcount();
		},
		// 
		getAcount(Param){
			var that = this;
			var gParam = Param? Param: this.param;
			this.axios.post(this.$baseUrl + '/students',gParam)
			.then((res)=>{
				console.log(res.data)
				var theJSON = {};
		        that.acntList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		stuNum: perData.username,
		        		name: perData.name? perData.name:'',
		        		// phone: perData.phone || '',
		        		phone: perData.phone?perData.phone:'',
		        		sex: perData.sexual? perData.sexual:''
		        	}
		        	return theJSON;
		        }
				if (res.data.code == 200) {
					res = res.data.data;
					that.totalNum = res.totalNumber;
					if (Array.isArray(res.pageData)) {
		        		for(var key of res.pageData){
			        		that.acntList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        	that.ispage = true;
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
			})
			.catch((e)=>{
				console.log(e)
			})
		},
		//
		upAcount(index){
			this.dialogFormVisible = true;
			this.dialogTitle = '';
			this.isUpPsd = true;
			this.form.name = this.acntList[index].stuNum;
		},
		//
		appAcount(){
			this.dialogFormVisible = true;
			this.isUpPsd = false;
			this.dialogTitle = '';
		},
		///
		simpleAcount(){
			var that = this;
			if (this.form.name == '') {
				this.$message.error('');
				return ;
			}
			var param = new FormData();
			param.append('token',this.getToken());
			if (this.dialogTitle == '') {
				param.append('username',this.form.name);
				this.axios.post(this.$baseUrl+'/student/add',param)
				.then((res)=>{
					console.log(res.data);
					if (res.data.code == 200) {
						that.$message.success('');
						this.dialogFormVisible = false;
						this.form.name = '';
						that.getAcount();
					} else {
						that.$message.error(res.data.message);
					}
				})
				.catch((e) =>{
					console.log(e);
				})
			} else {
				param.append('id',this.form.name);
				param.append('password',this.form.psd);
				this.axios.post(this.$baseUrl+'/stu/update',param)
				.then((res)=>{
					console.log(res.data);
					if (res.data.code == 200) {
						that.$message.success('');
						this.dialogFormVisible = false;
						this.form.name = '';
						this.form.psd = '';
						that.getAcount();
					}
				})
				.catch((e) =>{
					console.log(e);
				})
			}
		},
		// //
		// DelAcount(id){
		// 	var that = this;
		// 	this.$confirm('')
		// 	.then(()=>{
		// 		that.DoDel(id);
		// 	})
		// 	.catch(()=>{})
		// },
		// DoDel(id){
		// 	var that = this;
		// 	var Dparam = new FormData();
		// 	this.axios.post()
		// 	.then((res)=>{
		// 		// that.DoDel(id);
		// 		console.log(res);
		// 	})
		// 	.catch(()=>{})
		// },
		//
		Searching(){
			var that = this;
			var param = new FormData();
			param.append('token',this.param.get('token'));
			param.append('currentPage',this.param.get('currentPage'));
			param.append('pageNum',this.param.get('pageNum'));
			if (this.searches == '') {
				this.$message.error('');
				return ;
			} 
			if (this.searches[0] == '2') {
				param.append('stuNum',this.searches);
			} else if(this.searches[0] == '0') {
				param.append('classNum',this.searches);
			} else {
				param.append('stuName',this.searches);
			}
			console.log(param.get('pageNum'))
			this.getAcount(param);
		},
		//
		handleSuc(res, file, fileList){
			console.log(res);
			if (res.code == 200) {
				this.$message.success(res.message);
			}
		},
		handleErr(err, file, fileList){
			if (fileList.length == 0) {
				console.log('')
			}
			console.log(fileList);
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.StuAcount{
	width: 95%;
	margin: 0 auto;
	.stuHead{
		height: 65*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		li{
			text-align: center;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #316694;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 19*@allwidth;
		}
		.search{
			border: 1px solid grey;
			border-radius: 5px;
			input{
				width: 250*@allwidth;
				height: 36*@allwidth;
				line-height: 36*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				border-radius: 5px;
				outline: none;
				border: 0;
			}
			i{
				font-size: 22*@allwidth;
				font-weight: bold;
				padding-right: 10*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: red;
			}
		}
		.search:hover{
			border-color: #409eff;
		}
		.createAcount{
			line-height: 45*@allwidth;
			display: flex;
			li{
				background: #316694;
			}
			li:first-child{
				background: #226f40;
			}
		}
	}
	.stuAcnt{
		margin: 10*@allwidth 0;
		li .el-row {
			border-bottom: 1px solid #D2D2D2;
			text-align: center;
			.el-col{
				line-height: 33*@allwidth;
				font-size: 18*@allwidth;
			}
			i{
				line-height: 33*@allwidth;
				font-size: 25*@allwidth;
				padding: 0 5*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: #9CE76B;
			}
			i:last-child:hover{
				color: red;
			}
		}
		li:first-child{
			.el-row{
				background: rgba(144, 147, 153,0.3);
				.el-col{
					line-height: 40*@allwidth;
				}
			}
		}
		li:hover{
			background: rgba(144, 147, 153,0.1);
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
</style>