<template>
	<div class="course">
		<ul class="newsHead">
			<li></li>
  			<li @click="addClass()"></li>
  		</ul>
  		<ul class="showCourse">
  			<li v-for="(item,index) in DataList" :key="index">
  				<div class="name" @click="goCommunity(item.id)">{{item.name}}</div>
  				<div class="desc">
  					<p>{{item.desc}}</p>
  					<div class="doubI">
  						<i @click="UpClass(index)" class="el-icon-edit-outline"></i>
						<i @click="ifDelClass(item.id)" class="el-icon-delete"></i>
  					</div>
  				</div>
  			</li>
  		</ul>
  		<div class="paging">
	  		<el-pagination
	  		  v-if='ispage'
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="12"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
  		<el-dialog
  			title=""
		  	:visible.sync="dialogVisible"
		  	width="45%">
		  	<el-form :model="pubForm" class="demo-ruleForm" style="width: 85%;margin: 0 auto;">
				<el-form-item label="" prop="name" style="margin-bottom: 0;">
				    <el-input v-model="pubForm.title" placeholder=""  oninput = "value=value.replace(/[^\d]/g,'')"></el-input>
				</el-form-item>
				<el-form-item label="" prop="desc">
				    <el-input type="textarea" v-model="pubForm.content" placeholder=""></el-input>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="canselSpeach()"></el-button>
		    <el-button type="primary" @click="pubSpeach()"></el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			activePage: 1,
			totalPage: 1,
			classUrl: '',
			pubForm:{
				id: '',
				title: '',
				content: ''
			},
			DataList:[],
			testList:[{
				id: '12',
				name: '',
				desc: ''
			}]
		}
	},
    mounted(){
    	if (this.getToken()) {
    		this.getListParam();
    	}
    },
	methods:{
		//
		getListParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('token',this.getToken());
			this.param.append('currentPage',this.activePage);
			this.param.append('pageNum',12);
			this.classUrl = this.$baseUrl + '/class/show';
			this.realGotData();
		},
		//
		realGotData(){
			var that = this;
			this.axios.post(this.classUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.DataList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		name: perData.classNum,
		        		desc: perData.description
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.DataList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        	//
		        	if (that.totalPage > 12) {
		        		that.ispage = true;
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
		     })
			.catch((e)=>{
				console.log(e);
				that.DataList = that.testList;
			})
		},
		//
		addClass(){
			this.dialogVisible = true;
		},
		//
		canselSpeach(){
			this.pubForm.id = '';
			this.pubForm.title = '';
			this.pubForm.content = '';
			this.dialogVisible = false;
		},
		//
		pubSpeach(){
			if (this.pubForm.title == '') {
				this.$message.error('');
				return ;
			}
			if (this.pubForm.content == '') {
				this.$message.error('');
				return ;
			}
			var that = this;
			var pubSpeaParam = new FormData();
			var msg = '';
			var url = '';
			var id = this.pubForm.id;
			if (id) {
				url = this.$baseUrl + '/class/update';
				pubSpeaParam.append('id',id);
				msg = '';

			} else {
				url = this.$baseUrl + '/class/create';
				msg = '';
			}
			pubSpeaParam.append('token',this.param.get('token'));
			pubSpeaParam.append('classNum',this.pubForm.title);
			pubSpeaParam.append('description',this.pubForm.content);
			this.axios.post(url,pubSpeaParam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success(msg + '');
					//
					that.canselSpeach();
					that.realGotData();
				} else {
					that.$message.error(msg +'');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//
		UpClass(index){
			this.dialogVisible = true;
			this.pubForm.id = this.DataList[index].id;
			this.pubForm.title = this.DataList[index].name;
			this.pubForm.content = this.DataList[index].desc;
		},
		//
		ifDelClass(id){
			var that = this;
			this.$confirm('')
			.then(()=>{
				that.DoDel(id);
			})
			.catch(()=>{})
		},
		DoDel(id){
			var that = this;
			var url = this.$baseUrl + '/class/delete';
			var param = new FormData();
			param.append('token',this.getToken());
			param.append('id',id);
			this.axios.post(url,param)
			.then((res)=>{
				console.log(res);
				if (res.data.code == 200) {
					that.realGotData();
					that.$message.success('');
				}  else {
					that.$message.error('');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//
		goCommunity(id){
			this.$router.push('/Communicate/' + id);
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		if (theToken == null) {
    			return;
    		}
    		return theToken.slice(1,theToken.length-1);
    	},
    	//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li{
			width: 18%;
			height: 88*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.6%;
			cursor: pointer;
			.name{
				width: 65%;
				margin: 0 auto;
				font-size: 23*@allwidth;
				margin-top: 25*@allwidth;
			}
			.desc{
				width: 65%;
				margin: 0 auto;
				font-size: 13*@allwidth;
				margin-top: 10*@allwidth;
				display: flex;
				.doubI{
					display: none;
					margin-left: auto;
					i{
						padding: 0 1*@allwidth;
					}
					i:hover{
						color: red;
					}
					i:first-child:hover{
						color: blue;
					}
				}
			}
			.desc:hover{
				.doubI{
					display: block;
				}
			}
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
//element-ui 
.course /deep/ .el-dialog__body {
    padding: 0;
}
</style>