<template>
	<div class="StuProgress">
		<div class="stuHead">
			<ul>
				<li>{{stuTitle}} </li>
			</ul>
			<div class="search">
				<input v-model="searches" type="text" name="search" placeholder="" autocomplete="off">
				<i @click="Searching()" class="el-icon-search"></i>
			</div>
		</div>
		<ul class="stuTable">
			<li>
				<el-row>
				  <el-col :span="4"></el-col>
				  <el-col :span="4"></el-col>
				  <el-col :span="5"></el-col>
				  <el-col :span="4"></el-col>
				  <el-col :span="7"></el-col>
				</el-row>
			</li>
			<li v-for="(item,index) in TableList" :key="index">
				<el-row>
				  <el-col :span="4">{{item.acount}}</el-col>
				  <el-col :span="4">{{item.name}}</el-col>
				  <el-col :span="5">{{item.phone}}</el-col>
				  <el-col :span="4">{{item.graduated}}</el-col>
				  <el-col :span="7" style="padding-top: 1px;">
				  	<el-progress :percentage="item.percent"></el-progress>
				  </el-col>
				</el-row>
			</li>
		</ul>
		<div class="paging">
	  		<el-pagination
	  		  v-if='ispage'
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="12"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'StuProgress',
	data(){
		return {
			param: new FormData(),
			stuTitle: '',
			searching: '',
			activePage: 1,
			totalPage: 1,
			ispage: false,
			searches: '',
			TableList:[]
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addParamValue();
		}
	},
	methods:{
		//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//
			this.param.set('currentPage',this.activePage);
			this.getAcount()
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//
		addParamValue(){
			//activePage
			var courseId = sessionStorage.getItem('courseID');
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('token',this.getToken());
			this.param.append('currentPage',this.activePage);
			this.param.append('pageNum',10);
			this.param.append('course_id',courseId);
			this.getAcount();
		},
		// 
		getAcount(){
			var that = this;
			this.axios.post(this.$baseUrl + '/course/progress',this.param)
			.then((res)=>{
				console.log(res.data)
				var theJSON = {};
		        that.TableList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		acount: perData.student.username,
		        		name: perData.student.name,
		        		phone: perData.student.phone,
		        		graduated: perData.graduated == 0 ? '': '',
		        		percent: perData.learningProgress
		        	}
		        	return theJSON;
		        }
				if (res.data.code == 200) {
					res = res.data.data;
					that.totalNum = res.totalNumber;
					if (Array.isArray(res.pageData)) {
		        		for(var key of res.pageData){
			        		that.TableList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        	that.ispage = true;
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
			})
			.catch((e)=>{
				console.log(e)
			})
		},//
		Searching(){
			var that = this;
			var param = new FormData();
			param.append('token',this.param.get('token'));
			param.append('currentPage',this.param.get('currentPage'));
			param.append('pageNum',this.param.get('pageNum'));
			if (this.searches == '') {
				this.$message.error('');
				return ;
			} 
			if (this.searches[0] == '2') {
				param.append('stuNum',this.searches);
			} else if(this.searches[0] == '0') {
				param.append('classNum',this.searches);
			} else {
				param.append('stuName',this.searches);
			}
			console.log(param.get('pageNum'))
			this.getAcount(param);
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.StuProgress{
	width: 95%;
	margin: 0 auto;
	.stuHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:hover{
			opacity: 0.8;
		}
		.search{
			border: 1px solid grey;
			border-radius: 5px;
			input{
				width: 250*@allwidth;
				height: 40*@allwidth;
				line-height: 40*@allwidth;
				padding: 0 10*@allwidth;
				font-size: 18*@allwidth;
				border-radius: 5px;
				outline: none;
				border: 0;
			}
			i{
				font-size: 22*@allwidth;
				font-weight: bold;
				padding-right: 10*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: red;
			}
		}
		.search:hover{
			border-color: #409eff;
		}
	}
	.stuTable{
		margin: 10*@allwidth 0;
		li .el-row {
			border-bottom: 1px solid #D2D2D2;
			text-align: center;
			line-height: 33*@allwidth;
			.el-col{
				justify-content: center;
				font-size: 18*@allwidth;
			}
		}
		li:first-child{
			.el-row{
				background: rgba(144, 147, 153,0.3);
			}
		}
		li:hover{
			background: rgba(144, 147, 153,0.1);
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
</style>