<template>
	<div class="course">
		<ul class="newsHead">
			<li></li>
  		</ul>
  		<ul class="showCourse">
  			<li v-for="(item,index) in DataList" :key="index" @click="gotoPart(index)">
  				<p>{{item.tipmsg}}{{item.content}}</p>
				<p>{{item.name}}</p>
				<p>{{item.time}}</p>
  			</li>
  		</ul>
  		<div class="paging">
	  		<el-pagination
	  		  v-if='ispage'
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="6"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			activePage: 1,
			totalPage: 1,
			classUrl: '',
			pubForm:{
				id: '',
				title: '',
				content: ''
			},
			DataList:[]
		}
	},
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
		}
    },
	methods:{
		//
		getListParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('token',this.getToken());
			this.param.append('currentPage',this.activePage);
			this.param.append('pageNum',6);
			this.classUrl = this.$baseUrl + '/messages';
			this.realGotData();
		},
		//
		realGotData(){
			var that = this;
			this.axios.post(this.classUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
				// console.log(res);
		        var theJSON = {};
		        that.DataList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		name: perData.name,
		        		content: perData.content,
		        		contentId: perData.contentId,
		        		category: perData.category,
		        		tipmsg: '',
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
		        			let newone = formatData(key);
		        			if (key.category == 'rp') {
		        				newone.tipmsg = '';
		        			} else if (key.category == 'class') {
		        				newone.tipmsg = '';
		        			} else if (key.category == 'notice') {
		        				newone.tipmsg = '';
		        			} else {
		        				newone.tipmsg = '';
		        			}
		        			that.DataList.push(newone);
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        	//
		        	if (that.totalPage > 12) {
		        		that.ispage = true;
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
		     })
			.catch((e)=>{
				console.log(e);
			})
		},
		//
		gotoPart(index){
			var type = this.DataList[index].category;
			switch(type) {
				case 'rp':
					this.$router.push({
						path: `/Comment`,
						query:{
							id: this.DataList[index].contentId,
							name: 'msg'
						}
					})
					break;
				case 'class':
					this.$router.push({
						path: `/Comment`,
						query:{
							id: this.DataList[index].contentId,
							name: '123'
						}
					})
					break;
				case 'homework':
					break;
				case 'notice':
					break;
				default :
					break;
			}
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li{
			width: 100%;
			height: 50*@allwidth;
			border: 1.5px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.6%;
			padding: 0 20*@allwidth;
			cursor: pointer;
			font-size: 18*@allwidth;
			display: flex;
			align-items: center;
			p{
				width: 25%;
			}
			p:first-child{
				width: 50%;
			}
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
</style>