<template>
	<div class="showInform">
		<div class="showHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}</p>
			<div class="annouce" v-show="ifUP">
				<div @click="goCorrect()" class="selectStu"></div>
			</div>
		</div>
		<hr>
		<div class="content">
			<div class="newsTitle">{{showData.newsTitle}}</div>
			<div class="author">
				<p>:{{showData.publisher}}</p>
				<p>{{showData.publishTime}}</p>
			</div>
			<div class="newscontent" v-html="showData.newsContent"></div>
		</div>
		
	</div>
</template>

<script>
export default {
	name: 'showInform',
	mounted(){
		if (this.getToken()) {
			let query = this.$route.query
			console.log(query.name,query.id)
			this.getContent(query.name,query.id)
		}
	},
	methods:{
		//
		addStyle(){
			// font-style: italic;
			this.decorate = this.content.replace(/<p>/g,'<p style="display: block;margin-block-start: 1em;margin-block-end: 1em;margin-inline-start: 0px;margin-inline-end: 0px;font-size: 1.2987013vw;line-height:1.9vw;">')
			this.decorate = this.decorate.replace(/<strong>/g,'<strong style="font-weight: bold;">')
			this.decorate = this.decorate.replace(/<em>/g,'<em style="font-style: italic;">')
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		theToken = theToken.slice(1,theToken.length-1);
    		// console.log(theToken);
    		return theToken;
    	},
		//ID 
		getContent(name,id){
			var that = this;
			switch(name){
				case 'news':
					this.title = '';
					this.ifUP = true;
					this.getUrl = this.$baseUrl + '/news/'+id;
					break;
				case 'inform':
					this.title = '';
					this.getUrl = this.$baseUrl + '/notice/'+ id;
					break;
				case 'test':
					this.ifUP = true;
					this.title = '';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				case 'hw':
					this.ifUP = true;
					this.title = '';
					this.getUrl = this.$baseUrl + '/homework/'+ id;
					break;
				default :
					break;
			}
			this.name = name;
			this.param.append('id',id);
			this.param.append('token',this.getToken());
			this.getDetail(this.getUrl);
		},
		//
		getDetail(url){
			this.axios.post(url,this.param)
			.then((res)=>{
				console.log(res.data)
				if (res.data.code == 200) {
					this.showData.newsTitle = res.data.data.title;
					this.showData.newsContent = res.data.data.content;
					this.showData.publisher = res.data.data.publisherId;
					this.showData.publishTime = res.data.data.time;
					this.showData.ID = res.data.data.id;
				} else {
					this.$message.error('');
				}
			})
			this.addStyle();
		},
		//
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //
	    goCorrect(){
	    	this.$router.push({
				path: `/Publish`,
				query:{
					name: 'Re' + this.name,
					id: this.showData.ID,
					// theParam: this.param
				}
			})
	    }
	},
	data(){
		return {
      		param: new FormData(),
			title: '',
			getUrl:'',
			name: '',
			ifUP: false,
      		showData:{
      			ID: '',
      			newsTitle: '',
      			newsContent: '',
      			publisher: '',
      			publishTime: ''
      		},
			content: '<p>this is a p.</p><h1><strong style="color: rgb(0, 0, 0);">this is a strong.</strong><span style="color: rgb(0, 0, 0);">&nbsp;</span><sub style="color: rgb(0, 0, 0);">this is a sub</sub><span style="color: rgb(0, 0, 0);">&nbsp;</span><sup style="color: rgb(0, 0, 0);">this is a sup</sup>this is h1.</h1><h2>this si h2</h2><p><br></p><h3>thsi is h3</h3><h4>this is h4</h4><h5>thsi si h5</h5><h6>this is h6</h6><ul><li>31232</li><li>ouosdi</li></ul><ol><li>31232</li><li>ouosdi</li></ol><blockquote><em style="color: rgb(0, 0, 0);">this si em.</em>this si blockqout</blockquote><p><br></p>'
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.showInform{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		padding: 0;
		margin: 0;
		margin-bottom: 10*@allwidth;
	}
	.showHead{
		height: 65*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.annouce{
			margin-left: auto;
			display: flex;
			.selectStu{
				text-align: center;
				height: 45*@allwidth;
				line-height: 45*@allwidth;
				width: 100*@allwidth;
				color: #fff;
				background: #54CAAB;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 20*@allwidth;
			}
		}
	}
	.content{
		width: 85%;
		margin: 0 auto;
		height: calc(100vh - 170*@allwidth);
		overflow-x: hidden;
		overflow-y: auto;
		.newsTitle{
			font-size: 25*@allwidth;
			line-height: 50*@allwidth;
			font-weight: bold;
			text-align: center;
		}
		.author{
			margin-left: 59*@allwidth;
			display: flex;
			justify-content: flex-end;
			p{
				margin: 0 20*@allwidth;
				font-size: 16*@allwidth;
				opacity: 0.7;
			}
		}
	}
	.content::-webkit-scrollbar{
		display: none;
	}
}
</style>