<template>
	<div class="UpCourseWare">
		<ul class="newsHead">
			<li @click="goUpload()"></li>
			<li></li>
			<li @click="isadd=true" v-show="!isadd"></li>
		</ul>
		<div class="addWare" v-if="isadd">
			<el-upload
				class="upload-demo"
				ref="upload"
				action="#"
				multiple
				:on-change="handlePPT"
				:on-preview="handlePreview"
				:on-remove="handleRemove"
				:file-list="fileList"
				:auto-upload="false">
				<div class="addBtn" slot="trigger"></div>
				<div class="addBtn" @click="submitUpload"></div>
				<div style="border-top: 1px solid black; margin-top: 5px; padding-top: 10px;" slot="tip" class="el-upload__tip"></div>
			</el-upload>
		</div>
		<div class="PPTlist" v-else>
			<ul class="PList">
				<li>
					<p></p>
					<p></p>
					<p></p>
				</li>
				<li v-for="(item,index) in pptList" :key="index">
					<p>{{item.title}}</p>
					<p>{{item.time}}</p>
					<p><a :href="imgUrl+item.url" :download="item.title"><i class="el-icon-download"></i></a></p>
				</li>
			</ul>
			<div class="Page">
				<el-pagination
			      @current-change="handleCurrentChange"
			      :current-page.sync="activePage"
			      :page-size="10"
			      :pager-count="5"
			      layout="prev, pager, next"
			      :total="totalPage">
			    </el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'UpCourseWare',
	data(){
		return{
			imgUrl: '',
			param: new FormData(),
			courseID: '',
			totalPage: 1,
			activePage: 1,
			url: '',
			isadd: false,
			fileList:[],
			name: '',
			pptList: []
		}
	},
	mounted(){
		if (this.getToken()) {
			this.addWareParam();
			this.imgUrl = this.$ImgUrl + '/courseware/'; 
		}
	},
	methods: {
		//
		submitUpload() {
			var that = this;
			var uParam = new FormData();
			for(let key of this.fileList) {
				uParam.append('multipartFile',key.raw);
			}
			uParam.append('superiorId',this.courseID);
			uParam.append('token',this.getToken());
			this.axios.post(this.$baseUrl+'/uploadCourseware',uParam)
			.then((res)=>{
				console.log(res);
				if (res.data.code == 200) {
					that.$message.success('');
					this.isadd = false;
				} else {
					// that.$message.error('!');
					that.$message.error('PPT')
				}
			})
			.catch((e)=>{
				console.log(e)
			})
		},
		//
		handleRemove(file, fileList) {
			console.log(file, fileList);
		},
		handlePreview(file) {
			console.log(file);
		},
		//
		handlePPT(file,fileList){
			// console.log(fileList);
			this.fileList = fileList;
		},
		//
		goUpload(){
			this.$router.push('/CourseUpload');
		},
		//
		addWareParam(){
			var nowPage = sessionStorage.getItem('nowPage');
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.url = this.$baseUrl + '/coursewares';
			this.courseID = sessionStorage.getItem('courseID');
			this.param.append('id',this.courseID);
			this.param.append('token',this.getToken());
			this.param.append('pageNum',10);
			this.param.append('currentPage',this.activePage);
			this.viewWare();
		},
		//
		viewWare(){
			var that = this;
			this.axios.post(this.url,this.param)
			.then((res)=>{
				// console.log(res.data);
				var res = res.data;
				var theJSON = {};
		        that.pptList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		url: perData.url,
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	console.log(Array.isArray(res.data.pageData))
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.pptList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning('');
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
			})
		},
		//token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//page=1
			this.param.delete('currentPage');
			this.param.append('currentPage',this.activePage);
      		this.$router.push(`/News/${this.name}/page=${val}`);
			this.viewWare();
		},
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.UpCourseWare{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 15*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:first-child{
			background: #90a7a6;
		}
		li:nth-child(3){
			margin-left: auto;
			background: #28acc1;
		}
	}
	.addWare{
		width: 80%;
		margin: 0 auto;
		margin-top: 20*@allwidth;
		.addBtn{
			float: right;
			text-align: center;
			height: 38*@allwidth;
			line-height: 38*@allwidth;
			padding: 0 12*@allwidth;
			color: #fff;
			background: #348ce6;
			cursor: pointer;
			border-radius: 5px;
			font-size: 17*@allwidth;
		}
	}
	.PPTlist{
		width: 100%;
		.PList{
			margin: 10*@allwidth 0;
			li{
				display: flex;
				line-height: 40*@allwidth;
				font-size: 18*@allwidth;
				p:first-child{
					width: 70%;
					text-align: left;
					padding-left: 20*@allwidth;
				}
				p{
					text-align: center;
					width: 20%;
				}
				p:last-child{
					width: 10%;
					a{
						color: black;
						i{
							font-size: 22*@allwidth;
							font-weight: bold;
							cursor: pointer;
						}
					}
				}
			}
			li:first-child{
				background: #3c5d80;
				color: #fff;
				p{
					line-height: 40*@allwidth;
				}
			}
			li:first-child:hover{
				background: #3c5d80;
			}
			li:hover{
				background: #ecf5ff;
			}
		}
		.Page{
			margin-top: 15*@allwidth;
			.el-pagination{
				float: right;
				font-weight: normal;
			}
		}
	}
}
</style>