<template>
  <div class="Login">
    <div class="log">
      <img src="../assets/img/log.gif">
      <img src="../assets/img/text.gif">
    </div>
  	<div class="main">
      <p></p>
  		<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="60px"label-position="left" class="demo-ruleForm">
	  		<el-form-item label="" prop="mname">
	  			<el-input type="mname" v-model="ruleForm.mname" autocomplete="off" placeholder=""></el-input>
	  		</el-form-item>
	  		<el-form-item label="" prop="password">
	  			<el-input type="password" v-model="ruleForm.password" autocomplete="off" placeholder=""></el-input>
	  		</el-form-item>
	  		<el-form-item>
	  			<el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		    	<el-button @click="resetForm('ruleForm')"></el-button>
	  		</el-form-item>
		</el-form>
  	</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Login',
  data(){
    var validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
    };
    var validatePwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
    };
    return {
     	ruleForm: {
        mname: '',
        password: '',
      },
      rules: {
        mname: [
          { validator: validateName, trigger: 'blur' }
        ],
        password: [
          { validator: validatePwd, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          //
          // console.log(valid);
          this.getPermit();
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    getPermit(){
      var that = this;
      let param = new FormData();
      param.append('username',this.ruleForm.mname);
      param.append('isTeacher',1);
      param.append('password',this.ruleForm.password);
      // this.saveUserInfo('add the token');
      this.$axios.post(this.$baseUrl + '/login',param)
      .then((res)=>{
        console.log(res.data);
        res.data.code == 200? that.saveUserInfo(res.data.data.token):that.$message.error(res.data.message);
      })
      .catch((e)=>{
        console.log(e)
      })
    },
    // 
    saveUserInfo(data){
      let userInfo = {
        name: this.ruleForm.mname,
        psd: this.ruleForm.password
      };
      // console.log(data)
      sessionStorage.setItem('token', JSON.stringify(data));
      // sessionStorage.setItem('test', JSON.stringify('hello world'));
      sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
      this.$message.success('');
      this.$router.push('/News/news');
    }
  }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.Login{
	width: 100vw;
	height: 100vh;
	background-image: url('../assets/img/Loginback.gif');
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: center;
  .log{
    height: 70*@allwidth;
    position: absolute;
    top: 10*@allwidth;
    left: 30*@allwidth;
    img{
      height: 100%;
    }
  }
	.main{
		width: 25%;
		min-width: 230px;
		padding: 30px 40px 10px 40px;
		background: #fff;
		border-radius: 10px;
		box-shadow: 0px 0px 5px #3F838D;
    p{
      font-size: 20px;
      text-align: center;
      padding-bottom: 20px;
      opacity: 0.6;
      // color: #47AD80;
      font-weight: bold;
    }
	}
}
</style>
