<template>
	<div class="UpCourseWare">
		<ul class="newsHead">
			<li></li>
			<li @click="goWare()"></li>
			<!-- {{course.name}} -->
			<li @click="addCHP"></li>
		</ul>
		<el-row>
		  <el-col :span="6">
		  	<div class="courseTab">
		  		<ul v-for="(sec,index) in section" :key="index">
		  			<li @click="ClickSec(index)">
		  				<p :title="sec.name">{{index + 1}}{{sec.name}}</p>
		  				<i :class="sec.showMore?'el-icon-arrow-up':'el-icon-arrow-down'"></i>
		  			</li>
		  			<li v-show="sec.showMore"
		  			 @click="ChoiceChp(index,indx)" 
		  			 :class="[(choseSecChp.secIndex == index && choseSecChp.chpIndex == indx) ? 'isActive':'']" 
		  			 v-for="(chp,indx) in sec.chapter" 
		  			 :key="indx">
		  				<p :title="chp.title">{{indx + 1}}{{chp.title}}</p>
		  			</li>
		  			<li v-show="sec.showMore" @click="appchp(index)">
		  				<i class="el-icon-circle-plus"></i>
		  				
		  			</li>
		  		</ul>
		  	</div>
		  </el-col>
		  <el-col :span="18" v-if="ifuploaded">
		  	<div class="upVideo">
		  		<div class="SEC">
		  			<p>{{secname}}</p>
		  			<i class="el-icon-edit-outline" @click="resetName()" >
		  				<!-- title="" -->
		  			</i>
		  			<div class="operate">
		  				<i class="el-icon-delete" @click="Delete()"></i>
		  			</div>
		  		</div>
		  		<ul class="EditName" v-show="choseSecChp.theGrade == 'SEC'? 
			  		section[choseSecChp.secIndex].edit : 
			  		section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].edit">
		  			<li>
		  				<p></p>
		  				<input v-model="resetWeight.name" type="text" placeholder="">
		  			</li>
		  			<li>
		  				<p></p>
		  				<input v-model="resetWeight.weight" type="text" placeholder="" oninput="value=value.replace(/[^\d]/g,'')">
		  			</li>
		  			<li class="confirm">
		  				<p @click="noResetName()" style="background: grey;color: #fff;"></p>
		  				<p @click="setPerName()"></p>
		  			</li>
		  		</ul>
		  		<div v-if="choseSecChp.theGrade == 'chp'" class="showVideo">
		  			<div v-if="section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].url && noEdit" class="haveVideo">
		  				<video :src="imgUrl+ section[choseSecChp.secIndex].chapter[choseSecChp.chpIndex].url" alt=""  controls="controls"></video>
		  			</div>
		  			<div v-else class="uploadVideo">
		  				<el-upload class="avatar-uploader el-upload--text" 
			  			:action="uploadUrl" 
			  			:show-file-list="false" 
			  			:data="videoData"
			  			:on-success="handleVideoSuccess" 
			  			:before-upload="beforeUploadVideo" 
			  			:on-progress="uploadVideoProcess">
					        <div v-if="videoFlag == false" style="width: 80px; height: 80px;border: 1px solid grey;border-radius: 5px;display: flex;align-items: center;justify-content: center;">
					        	<i class="el-icon-plus avatar-uploader-icon" style="font-size: 30px;color: grey;"></i>
					        </div>
					        <el-progress v-if="videoFlag == true" 
					        type="circle"  style="margin-top:30px;"
					        :percentage="videoUploadPercent"></el-progress>
					    </el-upload>
					    <p class="text">MP4</p>
		  			</div>
		  		</div>
		  	</div>
		  </el-col>
		</el-row>
	</div>
</template>

<script>
export default {
	name: 'UpCourseWare',
	data(){
		return{
			imgUrl: '',
			getListUrl: '',
			param: new FormData(),
			uploadUrl: '',
			noEdit: true,
			videoFlag: false,
			videoUploadPercent: 0,
			videoData:{
				weight: '',
				title: '',
				superior_id: '',
				token: ''
			},
			course:{
				name: '',
				id: '',
			},
			choseSecChp: {
				theGrade: 'SEC',
				secIndex: '0',
				chpIndex: '0'
			},
			resetWeight: {
				name: '',
				weight: ''
			},
			secname: '',
			ifuploaded: false,
			isChp: false,
			originSec:{
				showMore: true,
				name: '',
				edit: false,
				chapter:[{
					title: '',
					videoUrl: '',
					edit: false
				}]
			},
			orginchp:{
				title: '',
				videoUrl: '',
				edit: false
			},
			section:[]
		}
	},
	// mounted(){
	// 	this.addGetParam();
	// },
	created(){
		if (this.getToken()) {
			this.addGetParam();
			this.imgUrl = this.$ImgUrl + '/video/';
		}
	},
	methods: {
		//
		addGetParam(){
			var url = this.$baseUrl + '/courseInfo/';
			this.course.id = sessionStorage.getItem('courseID');
			this.course.name = sessionStorage.getItem('courseName');
			this.param.append('token',this.getToken());
			this.getListUrl = url+this.course.id;
			this.getCourseTitle();
		},
		//
		getCourseTitle(){
			var that = this;
			this.axios.post(this.getListUrl,this.param)
	      	.then((response)=>{
	      		var res = response.data;
	      		console.log(res.data);
		        var theJSON = {};
		        that.section = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		url: perData.url,
		        		edit: false
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	//section
		        	if (res.data.chapterDtoList.length > 0) {
		        		for(var key of res.data.chapterDtoList){
		        			var sCHP = {
		        				showMore: false,
		        				edit: false,
		        				id: '',
		        				name: '',
		        				chapter: []
		        			};
		        			sCHP.id = key.id;
		        			sCHP.name = key.title;
		        			//chapter
		        			if (Array.isArray(key.sectionList)) {
		        				for( var innet of key.sectionList){
		        					sCHP.chapter.push(formatData(innet));
		        				}
		        				that.ifuploaded = true;
		        			} else {
		        				console.log('no chapter!');
		        			}
			        		that.section.push(sCHP);
			        	}
		        	} else {
		        		that.ifuploaded = false;
		        		that.$message.warning('');
		        		return ;
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
		        //
		        that.ClickSec(0);
		     })
			.catch((e)=>{
	      		// console.log('')
				that.$message.warning(e);
			})
		},
		//token,token " "
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		beforeUploadVideo(file) {
			if (this.videoData.title== '' || this.videoData.weight == '') {
				this.$message.warning('')
				return false;
			}
		    if (['video/mp4'].indexOf(file.type) == -1) {
		        this.$message.error('');
		        return false;
		    }
		},
		//
		uploadVideoProcess(event, file, fileList){
		    this.videoFlag = true;
		    this.videoUploadPercent = parseInt(file.percentage.toFixed(0));
		},
		//
		handleVideoSuccess(res, file) {
			//
		    this.videoFlag = false;
		    this.videoUploadPercent = 0;
		    console.log(res);
		    if(res.code == 200){
		    	this.$message.success('');
		    	this.resetWeight.name = '';
				this.resetWeight.weight = '';
				this.noEdit = true;
				this.getCourseTitle();
		    }else{
		        this.$message.error('');
		    }
		},
		//
		addCHP(){
			var that = this;
			this.section.push(this.originSec);
			this.ifuploaded = true;
			var addSec = new FormData();
			addSec.append('title',this.originSec.name);
			addSec.append('weight',1);
			addSec.append('token',this.getToken());
			addSec.append('superiorId',this.course.id);
			this.axios.post(this.$baseUrl+'/chapter/create',addSec)
			.then((res)=>{
				console.log(res);
				that.getCourseTitle();
			})
		},
		//
		appchp(index){
			this.section[index].chapter.push(this.orginchp)
		},
		//,
		ClickSec(index){
			this.section[index].showMore = !this.section[index].showMore;
			this.isChp = false;
			this.choseSecChp.theGrade = 'SEC';
			this.choseSecChp.secIndex = index;
			//
			this.secname = '' + (index + 1) + '  ' + this.section[index].name;
		},
		//,
		resetName(){
			if (this.choseSecChp.theGrade == 'SEC') {
				this.section[this.choseSecChp.secIndex].edit = true;
			} else {
				this.noEdit = false;
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = true;
			} 
		},
		//
		noResetName(){
			if (this.choseSecChp.theGrade == 'SEC') {
				this.section[this.choseSecChp.secIndex].edit = false;
			} else {
				this.noEdit = true;
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = false;
			} 
		},
		//
		setPerName(){
			var that = this;
			if (this.resetWeight.name == '') {
				this.$message.warning('');
				return ;
			}
			if (this.resetWeight.weight == '') {
				this.$message.warning('');
				return;
			}
			//
			if (this.choseSecChp.theGrade == 'SEC') {
				var addSec = new FormData();
				addSec.append('title',this.resetWeight.name);
				//
				var regu = "^([0-9])[0-9]*(\\.\\w*)?$";
				
				addSec.append('weight',this.resetWeight.weight);
				addSec.append('token',this.getToken());
				//
				let secIndex = this.choseSecChp.secIndex;
				if (this.section[secIndex].id) {
					//
					addSec.append('id',this.section[secIndex].id);
					this.axios.post(this.$baseUrl+'/chapter/update',addSec)
					.then((res)=>{
						console.log(res);
						that.getCourseTitle();
					})
				}
				// else {
				// 	//
				// 	addSec.append('superiorId',this.course.id);
				// 	this.axios.post(this.$baseUrl+'/chapter/create',addSec)
				// 	.then((res)=>{
				// 		console.log(res);
				// 		that.getCourseTitle();
				// 	})
				// }
				this.section[this.choseSecChp.secIndex].edit = false;
				this.resetWeight.name = '';
				this.resetWeight.weight = '';
			} 
			//
			else {
				//
				this.videoData.title = this.resetWeight.name;
				this.videoData.weight = this.resetWeight.weight;
				this.videoData.superiorId = this.section[this.choseSecChp.secIndex].id;
				this.videoData.token = this.getToken();
				//url
				let baseInfo = this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex];
				if (baseInfo.id) {
					this.videoData.id = baseInfo.id;
					this.uploadUrl = this.$baseUrl+'/section/update';
				} else {
					this.uploadUrl = this.$baseUrl+'/section/create';
				}
				this.section[this.choseSecChp.secIndex].chapter[this.choseSecChp.chpIndex].edit = false;
			}
		},
		//index
		ChoiceChp(sec,chp){
			this.isChp = true;
			this.choseSecChp.theGrade = 'chp';
			this.choseSecChp.secIndex = sec;
			this.choseSecChp.chpIndex = chp;
			this.secname = (sec+1) + '-' + (chp+1) + '' + this.section[sec].chapter[chp].title;
		},
		//
		Delete(){
			var that = this;
			this.$confirm('')
			.then(()=>{
				var index = that.choseSecChp;
				if (index.theGrade == 'SEC') {
					that.DeleteDOM(that.$baseUrl+'/chapter/delete/'+ that.section[index.secIndex].id);
				} else {
					if (that.section[index.secIndex].chapter[index.chpIndex].id) {
						that.DeleteDOM(that.$baseUrl+'/section/delete/'+ that.section[index.secIndex].chapter[index.chpIndex].id);
					} else {
						that.getCourseTitle();
					}
					
				}
			})
		},
		//
		DeleteDOM(url){
			var that = this;
			var dparam = new FormData();
			dparam.append('token',this.getToken());
			this.axios.post(url,dparam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success('');
					that.getCourseTitle();
				} else {
					that.$message.error('');
				}
			})
		},
		//
		goWare(){
			this.$router.push('/CourseWare');
		}
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.UpCourseWare{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			padding: 0 15*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		li:nth-child(2){
			background: #90a7a6;
		}
		li:nth-child(3){
			margin-left: auto;
			background: #28acc1;
		}
	}
	.courseTab{
		margin-top: 5*@allwidth;
		ul{
			width: 100%;
			margin: 1px 0;
			border-radius: 5px;
			border: 1px solid #D2D2D2;
			li{
				height: 40*@allwidth;
				line-height: 40*@allwidth;
				font-size: 16*@allwidth;
				padding: 0 2*@allwidth;
				cursor: pointer;
				border-top: 0.3px solid #D2D2D2;
				p{
					margin-left: 15%;
					//
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					//end
				}
			}
			li:first-child{
				background: pink;
				height: 45*@allwidth;
				display: flex;
				align-items: center;
				justify-content: space-between;
				padding: 0 15*@allwidth;
				font-size: 18*@allwidth;
				p{
					margin: 0;
				}
			}
			li:last-child{
				text-align: center;
			}
			.isActive{
				color: #68CDBC;
				background: #F3F6F6;
			}
		}
	}
	.upVideo{
		width: 97%;
		margin: 0 auto;
		margin-top: 5*@allwidth;
		border: 1px solid grey;
		border-radius: 5px;
		.SEC{
			width: 95%;
			margin: 15*@allwidth auto;
			display: flex;
			font-size: 20*@allwidth;
			.operate{
				margin-left: auto;
			}
			i{
				margin: 0 5*@allwidth;
				cursor: pointer;
			}
			i:hover{
				color: #98E26E;
			}
		}
		.EditName{
			width: 95%;
			margin: 0 auto;
			padding: 10*@allwidth 0;
			border-top: 1px solid green;
			display: flex;
			justify-content: space-between;
			align-items: center;
			li{
				display: flex;
				align-items: center;
				font-size: 18*@allwidth;
				p{
					margin: 0 15*@allwidth;
				}
				input{
					outline: none;
					border: 0.5px solid black;
					border-radius: 3px;
					height: 30*@allwidth;
					padding-left: 5*@allwidth;
				}
				input:hover{
					border-color: #409EFF;
				}
			}
			.confirm{
				p{
					margin: 0 5*@allwidth;
					line-height: 34*@allwidth;
					background: #409EFF;
					color: #fff;
					border-radius: 5px;
					padding: 0 10*@allwidth;
					cursor: pointer;
				}
			}
		}
		.showVideo{
			width: 95%;
			margin: 0 auto;
			padding-top: 10*@allwidth;
			border-top: 1px solid green;
			.haveVideo{
				width: 100%;
				height: calc(100% - 30*@allwidth);
				display: flex;
				align-items: center;
				justify-content: center;
				video{
					width: 90%;
				}
			}
			.uploadVideo{
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin: 30*@allwidth 0;
				p{
					margin: 10*@allwidth 0;
				}
			}
		}
	}
}
</style>