<template>
	<div class="addCourse">
		<div class="pubHead">
			<i class="el-icon-back" @click="goBack()"></i>
			<p>{{title}}</p>
			<div @click="checkAndUp()" class="selectStu">{{title}}</div>
		</div>
		<hr>
		<div class="mainInfo">
			<el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" label-position="left">
				<el-form-item label="" prop="name">
				    <el-input v-model="ruleForm.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="desc">
				    <el-input type="textarea" v-model="ruleForm.desc" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="invite">
				    <el-input v-model="ruleForm.invite" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="cover">
				  	<el-upload
					  action="#"
					  list-type="picture-card"
					  :class="{disabled: isFine}"
	  				  :on-remove="handleRemove"
	  				  :on-change="handleChange"
					  :auto-upload="false">
					    <i slot="default" class="el-icon-plus"></i>
					</el-upload>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
export default {
	name: 'addCourse',
	data(){
		var validateName = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	          callback();
	        }
	    };
	    var validatePwd = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	          callback();
	        }
	    };
	    var validateInvite = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	          callback();
	        }
	    };
		return {
      		param: new FormData(),
      		isFine: false,
      		title: '',
			ruleForm:{
				name: '',
				desc: '',
				cover:'',
				id: '',
				invite: ''
			},
			rules: {
	          name: [
	            { validator: validateName, trigger: 'blur' }
	          ],
	          desc: [
	            { validator: validatePwd, trigger: 'blur' }
	          ],
	          invite: [
	            { validator: validateInvite, trigger: 'blur' }
	          ]
	        },
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getOldInfo();
		}
	},
	methods:{
		//
		handleRemove(file, fileList) {
	        // console.log(file, fileList);
	        console.log(fileList.length)
	        if (fileList.length < 1) {
	        	this.isFine = false;
	        }
	    },
		//
		goBack() {
	       console.log('go back');
	       this.$router.go(-1)
	    },
	    //token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
	    //
	    checkAndUp(){
	    	var url = '';
	    	if (this.ruleForm.name == '') {
	    		this.$message.error('');
	    		return ;
	    	}
	    	if (this.ruleForm.desc == '') {
	    		this.$message.error('');
	    		return ;
	    	}
	    	if (this.ruleForm.invite == '') {
	    		this.$message.error('');
	    		return ;
	    	}
	    	if (this.param.get('pic') == '') {
	    		this.$message.error('');
	    		return ;
	    	}
	    	this.param.append('title',this.ruleForm.name);
	    	this.param.append('code',this.ruleForm.invite);
	    	this.param.append('description',this.ruleForm.desc);
			this.param.append('token',this.getToken());
			//
			if (this.$route.query.id) {
				this.param.append('id',this.ruleForm.id);
				url = this.$baseUrl+'/course/update';
			} else {
				url = this.$baseUrl+'/course/create';
			}
			// console.log(url)
			this.createCourse(url);
	    },
	    ///
	    createCourse(url){
	    	var that = this;
	    	this.axios.post(url,this.param)
	    	.then((res)=>{
	    		if (res.data.code == 200) {
	    			that.$message.success(that.title + '');
	    			that.$router.push('/course');
	    		} else {
	    			that.$message.error(that.title + '');
	    		}
	    	})
	    	.catch((e)=>{
	    		console.log(e);
	    	})
	    },
	    //url
	    handleChange(file,fileList){
	    	// console.log(file)
	    	//
	    	this.param.append('pic',file.raw);
	    	console.log(fileList.length)
	    	if (fileList.length >= 1) {
	    		this.isFine = true;
	    	}
	    	// console.log(file.url)
	    	// console.log(file.raw)
	    },
	    //
	    getOldInfo(){
	    	var that = this;
	    	var query = this.$route.query;
	    	if (query.id) {
	    		this.title = ''
	    		this.ruleForm.name = query.name;
	    		this.ruleForm.id = query.id;
	    		this.ruleForm.cover = query.cover;
	    		this.ruleForm.desc = query.desc;
	    		this.ruleForm.code = query.code;
	    	}
	    }
	}
}
</script>

<style lang="less">
@allwidth: 100/1386vw;
.addCourse{
	width: 95%;
	margin: 0 auto;
	hr{
		border: 0.8px solid rgb(61, 126, 141);
		margin: 0;
	}
	.pubHead{
		width: 100%;
		height: 65*@allwidth;	
		font-size: 22*@allwidth;
		display: flex;
		align-items: center;
		i{
			font-weight: bold;
		}
		p{
			padding-left: 40*@allwidth;
		}
		.selectStu{
			margin-left: auto;
			text-align: center;
			padding: 0 20*@allwidth;
			height: 45*@allwidth;
			line-height: 45*@allwidth;
			color: #fff;
			background: #226352;
			cursor: pointer;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
	}
	.mainInfo{
		width: 60%;
		margin: 40*@allwidth 40*@allwidth;
	}
}
.disabled .el-upload--picture-card{
    display: none;
    background: red;
}
</style>