<template>
	<div class="course">
		<ul class="newsHead">
			<li></li>
			<li @click="addCourse"></li>
		</ul>
		<ul class="showCourse">
			<li v-for="(item,index) in courseList" :key="item.id" class="courseList">
				<img :src="imgUrl + item.cover" alt="123" @click="goCourse(item.id,item.name)">
				<ul class="info">
					<li @click="goCourse(item.id,item.name)">{{item.name}}</li>
					<li>
					<p>{{item.teacher}}</p>
					<div class="change">
						<i @click="UpCourse(index)" class="el-icon-edit-outline"></i>
						<i @click="ifDelCourse(item.id)" class="el-icon-delete"></i>
					</div>
					</li>
				</ul>
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'course',
	data(){
		return {
			imgUrl: '',
			param: new FormData(),
			courseList: [],
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getCourseList();
			this.imgUrl = this.$ImgUrl + '/course/';
		}
	},
	methods:{
		//
		getCourseList(){
			var that = this;
			this.param.append('currentPage',1);
			this.param.append('pageNum',9);
			this.param.append('token',this.getToken());
			// this.axios.post(this.$baseUrl + '/courses',this.param)
			this.axios.post(this.$baseUrl + '/courses',this.param)
			.then((res)=>{
				console.log(res.data);
				var theJSON = {};
				that.courseList = [];
				var formatData = function(perData){
					theJSON = {
						id: perData.id,
						teacher: perData.teaName,
						name: perData.title,
						cover: perData.url,
						desc: perData.description,
						code: perData.code
					}
					return theJSON;
				}
				if (res.data.code == 200) {
					var data = res.data.data.pageData;
					if (Array.isArray(data)) {
						for(let key of data){
							that.courseList.push(formatData(key));
						}
					} else {
						that.$message.warning( '');
					}
				} else {
					that.$message.error('');
				}
			})
			.catch((e)=>{
				that.$message.error(e);
			})
		},
		//token,token " "
		getToken(){
			var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		},
		//
		goCourse(id,name) {
			sessionStorage.setItem('courseID', JSON.stringify(id));
			sessionStorage.setItem('courseName', JSON.stringify(name));
			this.$router.push('/News/inform');
		},
		//
		addCourse(){
			this.$router.push('/CourseAdd');
		},
		//
		UpCourse(index){
			this.$router.push({
				path: `/CourseAdd`,
				query:{
					id: this.courseList[index].id,
					name: this.courseList[index].name,
					desc: this.courseList[index].desc,
					cover: this.courseList[index].cover,
					code: this.courseList[index].code
				}
			});
		},
		//
		ifDelCourse(id){
			var that = this;
			this.$confirm('')
			.then(()=>{
				that.deleteList(id)
			})
			.catch(()=>{})
		},
		// 
		deleteList(id){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl =  this.$baseUrl + '/course/delete/' + id;
			Dparam.append('token',this.getToken());
			// Dparam.append('')
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				if (res.data.code == 200) {
					this.getCourseList(this.getDataUrl);
					that.$message.success('');
				} else {
					that.$message.error('');
				}
			})
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 55*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			min-width: 65*@allwidth;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 10*@allwidth;
			margin: 0 3*@allwidth;
			font-size: 20*@allwidth;
			color: #fff;
			background: #099c4a;
			border-radius: 5px;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
			cursor: pointer;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		.courseList{
			width: 23.5%;
			height: 88*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.3%;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			img{
				width: 40%;
				height: 85%;
				border-radius: 5px;
				margin: 0;
			}
			.info{
				width: 50%;
				height: 75%;
				margin-left: 10*@allwidth;
				position: relative;
				li{
					width: 100%;
					font-size: 18*@allwidth;
					font-weight: bold;
					display: flex;
					p{
						font-weight: normal;
						font-size: 14*@allwidth;
					}
					.change{
						margin-left: auto;
						display: none;
						i{
							margin: 0 1px;
						}
						i:hover{
							color: #59CCA8;
						}
					}
				}
				li:last-child{
					position: absolute;
					bottom: 3*@allwidth;
					font-size: 15*@allwidth;
				}
			}
			.info:hover{
				.change{
					display: block;
				}
			}
		}
	}
}
</style>