<template>
	<div class="course">
		<ul class="newsHead">
			<li v-if="name != 'msg'"><i class="el-icon-back" @click="goBack()"></i></li>
			<li v-if="name != 'msg'">{{title}}</li>
			<li v-if="name == 'msg'" style="background: #099c4a;color: #fff;border-radius: 5px;">{{title}}</li>
  			<li @click="dialogVisible = true" ></li>
  		</ul>
  		<ul class="TieList">
			<li v-for="(item,index) in publishData" :key="index" @click="PartDetail(index)">
				<img :src="imgUrl +item.cover">
				<div class="part">
					<p>{{item.publisher}}<span v-if="item.rank"></span><span>{{item.time}}</span></p>
					<p>{{item.title}}</p>
					<p>:{{item.commentNum}}</p>
				</div>
			</li>	
		</ul>
  		<div class="paging">
	  		<el-pagination
	  		  v-if="ispage"
		      @current-change="handleCurrentChange"
		      :current-page.sync="activePage"
		      :page-size="12"
		      :pager-count="5"
		      layout="prev, pager, next"
		      :total="totalPage">
		    </el-pagination>
	  	</div>
  		<el-dialog
  			title=""
  			center
		  	:visible.sync="dialogVisible"
		  	width="80%">
		  	<el-form :model="pubForm" class="demo-ruleForm" style="width: 85%;margin: 0 auto;">
				<el-form-item label="" prop="name" style="margin-bottom: 0;">
				    <el-input v-model="pubForm.title" placeholder=""></el-input>
				</el-form-item>
				<el-form-item label="" prop="desc">
				    <br>
				    <Edit @newsEvent="showInfo"></Edit>
				</el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="canselSpeach()"></el-button>
		    <el-button type="primary" @click="isSpeach()"></el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import Edit from '../components/ue2.vue';
export default {
	name: 'course',
	components:{
		Edit
	},
	data(){
		return {
			imgUrl: '',
			param: new FormData(),
			dialogVisible: false,
			ispage: false,
			title: '',
			name: '',
			activePage: 1,
			totalPage: 1,
			TieUrl: '',
			pubForm:{
				title: '',
				content: ''
			},
			classList:[{
				id: '2',
				name: '049117'
			}],
			publishData:[]
		}
	},
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
    		this.imgUrl = this.$ImgUrl + '/avatar/';
		}
    },
	methods:{
		//
		getListParam(){
			var name = this.$route.params.name;
			this.name = name;
			if (name == 'msg') {
				this.title = '';
				var courseId = sessionStorage.getItem('courseID');
				this.param.append('courseId',courseId);
				this.TieUrl = this.$baseUrl + '/courseDiscussion/questions/' + courseId;
			} else {
				this.title = '';
				this.TieUrl = this.$baseUrl + '/ClassDiscussion/questions/' + name;
			}
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('token',this.getToken());
			this.param.append('currentPage',this.activePage);
			this.param.append('pageNum',12);
			this.realGotData();
		},
		//
		realGotData(){
			var that = this;
			this.axios.post(this.TieUrl,this.param)
			.then((response)=>{
	      		var res = response.data;
		        var theJSON = {};
		        that.publishData = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		TieID: perData.id,
		        		publisher: perData.userDto.name,
		        		cover: perData.userDto.avatar,
		        		title: perData.title,
		        		rank: perData.isTeacher==1?true:false,
		        		commentNum: perData.commentNum,
		        		time: perData.time
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data)
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.publishData.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning(that.name + '');
		        	}
		        	that.ispage = true;
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
		     })
			.catch((e)=>{
				console.log(e);
			})
		},
		//
		canselSpeach(){
			this.pubForm.title = '';
			this.pubForm.content = '';
			this.dialogVisible = false;
		},
		isSpeach(){
			if (this.pubForm.title == '' || this.pubForm.content == '') {
				this.$message.error('!');
				return ;
			}
			this.pubSpeach();
		},
		//
		pubSpeach(){
			var that = this;
			var pubSpeaParam = new FormData();
			var url = '';
			if (this.name == 'msg') {
				url = this.$baseUrl + '/courseDiscussion/pub';
				pubSpeaParam.append('courseId',this.param.get('courseId'));
			} else {
				url = this.$baseUrl + '/ClassDiscussion/pub';
				pubSpeaParam.append('classId',this.name);
			}
			pubSpeaParam.append('token',this.param.get('token'));
			pubSpeaParam.append('title',this.pubForm.title);
			pubSpeaParam.append('content',this.pubForm.content);
			this.axios.post(url,pubSpeaParam)
			.then((res)=>{
				console.log(res.data);
				if (res.data.code == 200) {
					that.$message.success('');
					that.pubForm.title = '';
					that.pubForm.content = '';
					that.dialogVisible = false;
					that.realGotData();
				} else {
					that.$message.error('');
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
		//
		goCommunity(index){
			this.$router.push({
				path: `/CommunityPart`,
				query:{
					name: this.classList[index].name,
					id: this.classList[index].id
				}
			})
		},
		//
		PartDetail(index){
			this.$router.push({
				path: `/Comment`,
				query:{
					id: this.publishData[index].TieID,
					name: this.name
				}
			})
		},
		//token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	},
    	//
		goBack() {
			this.$router.go(-1);
		},
    	//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			//
			this.param.set('currentPage',this.activePage);
			this.realGotData();
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		//
		showInfo(e){
			this.pubForm.content = e;
		}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.course{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 55*@allwidth;
		padding-bottom: 5*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 40*@allwidth;
			line-height: 40*@allwidth;
			padding: 0 20*@allwidth;
			font-size: 20*@allwidth;
		}
		li:last-child{
			margin-left: auto;
			background: #28acc1;
			color: #fff;
			cursor: pointer;
			padding: 0 20*@allwidth;
			border-radius: 5px;
		}
	}
	.TieList{
		width: 95%;
		margin: 10*@allwidth auto;
		height: calc(100vh - 230*@allwidth);
		min-height: 230px;
		overflow-x: hidden;
		overflow-y: auto;
		li{
			display: flex;
			align-items: flex-start;
			// height: 70*@allwidth;
			padding: 5*@allwidth 0;
			border-bottom: 1px solid #D2D2D2;
			img{
				width: 40*@allwidth;
				height: 40*@allwidth;
				border-radius: 50%;
				margin: 0 15*@allwidth;
			}
			.part{
				cursor: pointer;
				p{
					font-size: 16*@allwidth;
					padding: 4*@allwidth 0;
					color: #406599;
					span{
						opacity: 0.8;
						font-size: 13*@allwidth;
						margin-left: 12*@allwidth;
					}
					span:first-child{
						border: 1px solid green;
						font-size: 12*@allwidth;
					}
				}
				p:nth-child(2){
					color: black;
					font-size: 18*@allwidth;
				}
			}
		}
		li:hover{
			background: #ecf5ff;
		}
	}
	.showCourse{
		width: 100%;
		margin-top: 5*@allwidth;
		display: flex;
		flex-wrap: wrap;
		li{
			width: 18%;
			height: 88*@allwidth;
			border: 2px solid #D2D2D2;
			border-radius: 5px;
			margin: 5*@allwidth 0.6%;
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}

//element-ui 
.course /deep/ .el-dialog__body {
    padding: 0;
}
</style>