<template>
  <div class="news">
  	<div class="newsHead">
  		<ul class="tabs">
  			<li :style="category==''? '':{background: '#90a7a6'}" @click="$router.push('/Exam/test')"></li>
  			<li :style="category==''? '':{background: '#90a7a6'}" @click="$router.push('/Exam/hw')"></li>
  		</ul>
  		<ul class="add">
  			<li @click="publish()">{{category}}</li>
  		</ul>
  	</div>
  	<div class="showList">
  		<el-row v-for="(list,index) in showList" :key="index">
		  <el-col :span="22" @click.native="showWork(list.id)">
		  	<div class="main">
		  		<p>{{list.title}}</p>
		  		<p> {{list.startTime}}  {{list.endTime}}</p>
		  	</div>
		  	<div class="graded"></div>
		  </el-col>
		  <el-col :span="2">
		  	<div class="operate">
		  		<i @click="Tdetails(list.id)" class="el-icon-document" title=""></i>
		  		<i @click="TDelete(list.id)" class="el-icon-delete" title=""></i>
		  	</div>
		  </el-col>
		</el-row>
  	</div>
  	<div class="paging">
  		<el-pagination
	      @current-change="handleCurrentChange"
	      :current-page.sync="activePage"
	      :page-size="5"
	      :pager-count="5"
	      layout="prev, pager, next"
	      :total="totalPage">
	    </el-pagination>
  	</div>
  </div>
</template>

<script>
export default{
	name: 'news',
	data() {
      return {
		param: new FormData(),
      	activePage: 1,
      	totalPage: 1, 
      	category: '',
      	name: '',
      	url: '',
        showList:[]
      };
    },
    mounted(){
    	if (this.getToken()) {
			this.getListParam();
		}
    },
    methods: {
    	getListParam(){
			var courseID = sessionStorage.getItem('courseID');
			this.url = this.$baseUrl + '/homework/show';
    		let name = this.$route.params.name;
			if (name == 'test') {
				this.category = '';
			} else {
				this.category = '';
			}
			this.name = name;
			var nowPage = sessionStorage.getItem('nowPage');
			console.log(nowPage)
			this.activePage = nowPage ? parseInt(nowPage):this.activePage;
			this.param.append('courseId',courseID);
    		this.param.append('category',name);
    		this.param.append('token',this.getToken());
			this.param.append('pageNum',8);
			this.param.append('currentPage',this.activePage);
			this.getListData();
    	},
    	getListData(){
    		var that = this;
    		this.axios.post(this.url,this.param)
    		.then((res)=>{
    			res = res.data;
    			console.log(res);
    			var theJSON = {};
		        that.showList = [];
		        var formatData = function(perData){
		        	theJSON = {
		        		id: perData.id,
		        		title: perData.title,
		        		publisher: perData.publisherId,
		        		startTime: perData.startTime,
		        		endTime: perData.endTime
		        	}
		        	return theJSON;
		        }
		        if (res.code == 200) {
		        	console.log(res.data);
		        	that.totalPage = res.data.totalNumber;
		        	if (Array.isArray(res.data.pageData)) {
		        		for(var key of res.data.pageData){
			        		that.showList.push(formatData(key));
			        	}
		        	} else {
		        		that.$message.warning('');
		        	}
		        } else {
		        	that.$message.error(''+ that.name +'');
		        }
    		})
    	},
    	//
		handleCurrentChange(val) {
			console.log(`: ${val}`);
			this.param.set('currentPage',this.activePage);
			this.getListData();
			//
			sessionStorage.setItem('nowPage', JSON.stringify(this.activePage));
		},
		///
		Tdetails(id){
			this.$router.push({
				path: `/Show`,
				query:{
					name: this.name,
					id: id
				}
			});
		},
		TDelete(id){
			var that = this;
			this.$confirm('')
			.then(()=>{
				that.deleteList(id)
			})
			.catch(()=>{})
		},
		// 
		deleteList(id){
			var that = this;
			var Dparam = new FormData();
			var DdataUrl = this.$baseUrl + '/homework/delete';
			Dparam.append('id',id);
			Dparam.append('token',this.getToken());
			this.axios.post(DdataUrl,Dparam)
			.then((res)=>{
				console.log(res)
				if (res.data.code == 200) {
					this.getListData();
					that.$message.success('');
				} else {
					that.$message.error('');
				}
			})
		},
		///
		publish(){
			this.$router.push({
	          path: `/Publish`,
				query:{
					name: this.name,
					// id: 
				}
	        })
		},
		//
		showWork(id){
			console.log(id)
			this.$router.push({
				path: `/Homework`,
				query:{
					name: this.name,
					id: id,
					// page: 1
				}
			})
		},
		//token
		getToken(){
		var theToken = sessionStorage.getItem('token');
			return theToken.slice(1,theToken.length-1);
		}
    }
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.news{
	width: 95%;
	margin: 0 auto;
	.newsHead{
		height: 70*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		li{
			text-align: center;
			height: 45*@allwidth;
			width: 100*@allwidth;
			color: #fff;
			background: #099c4a;
			cursor: pointer;
			margin: 0 3*@allwidth;
			border-radius: 5px;
			font-size: 20*@allwidth;
		}
		.tabs{
			display: flex;
			line-height: 45*@allwidth;
		}
		.add{
			margin-left: auto;
			line-height: 45*@allwidth;
			li{
				background: #28acc1;
			}
		}
	}
	.showList{
		padding-top: 18*@allwidth;
		.el-row{
			height: 55*@allwidth;
			border: 1px solid grey;
			border-radius: 5px;
			font-size: 14*@allwidth;
			padding: 0 20*@allwidth;
			margin-bottom: 4*@allwidth;
			color: #999999;
			display: flex;
			.el-col{
				display: flex;
				align-items: center;
				cursor: pointer;
				.main{
					display: flex;
					align-items: center;
					margin: 5*@allwidth 0;
					p{
						font-size: 18*@allwidth;
						color: #656565;
					}
					p:nth-child(2){
						font-size: 14*@allwidth;
						color: #999999;
						opacity: 0.7;
						margin-left: 30*@allwidth;
					}
				}
				.graded{
					margin-left: auto;
					opacity: 1;
					height: 40*@allwidth;
					line-height: 40*@allwidth;
					text-align: center;
					width: 40*@allwidth;
					border-radius: 50%;
					background: grey;
					color: #fff;
					font-size: 14*@allwidth;
				}
			}
			.operate{
				font-size: 25*@allwidth;
				margin-left: 16*@allwidth;
				i{
					padding: 0 4*@allwidth;
				}
				i:hover{
					color: #98E26E;
				}
			}
		}
		.el-row:hover{
			border: 2px solid #58CD77;
		}
	}
	.paging{
		margin-top: 15*@allwidth;
		.el-pagination{
			float: right;
			font-weight: normal;
		}
	}
}
</style>