<template>
	<div class="selfInfo">
		<div class="pubHead">
			<p></p>
			<ul>
				<li v-if="!isEdit" @click="upInfo()"></li>
				<li v-if="isEdit" @click="cancle()"></li>
				<li v-if="isEdit" @click="submit()"></li>
			</ul>
		</div>
		<div v-if="!isEdit" class="showInfo">
			<img :src="imgUrl + teacher.cover" alt="">
			<ul>
				<li>{{teacher.name}}</li>
				<li>
					<p>{{teacher.username}}</p>
					<p>{{teacher.email}}</p>
				</li>
			</ul>
		</div>
		<div v-if="isEdit" class="upInfo">
			<el-row>
				<el-col :span="8" v-if="upImg == ''? false:true">
					<img :src="upImg">
				</el-col>
				<el-col :span="8" v-show="upImg != ''? false:true">
					<el-upload
					  class="avatar-uploader"
					  :action="$baseUrl+'/uAvatar'"
					  list-type="picture-card"
					  auto-upload
					  accept="*.png,*.jpg,*.jpeg"
					  :show-file-list="false"
					  :on-success="handleSuc"
					  :on-error="handleErr"
					  :data="ImgCover">
					    <i slot="default" class="el-icon-plus"></i>
					</el-upload>
					<!-- <p style="line-height: 40px;font-size: 16px;margin-left: 50px;"></p> -->
				</el-col>
				<el-col :span="16">
					<el-form :model="ruleForm" ref="ruleForm" :rules="rules" label-width="100px" class="demo-ruleForm" label-position="left">
						<el-form-item label="" prop="name">
						    <el-input v-model="ruleForm.name" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="psd">
						    <el-input v-model="ruleForm.psd" placeholder=""></el-input>
						</el-form-item>
						<el-form-item label="" prop="email">
						    <el-input v-model="ruleForm.email" placeholder=""></el-input>
						</el-form-item>
					</el-form>
				</el-col>
			</el-row>
		</div>
	</div>
</template>

<script>
export default {
	name: 'selfInfo',
	data(){
		var validateName = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	          callback();
	        }
	    };
	    var validatePwd = (rule, value, callback) => {
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	          callback();
	        }
	    };
	    var validateEmail = (rule, value, callback) => {
	    	
	        if (value === '') {
	          callback(new Error(''));
	        } else {
	        	var pattern = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
	        	// console.log('the email is ' + pattern.test(value));
	        	if(pattern.test(value) == true) {
	        		callback();
		        } else {
		        	callback(new Error(''));
		        }
	        }
	    };
		return {
			imgUrl: '',
      		param: new FormData(),
      		upImg: '',
      		isEdit: false,
      		isUpImg: false,
      		ImgCover:{
      			token: ''
      		},
			ruleForm:{
				name: '',
				email: '',
				cover:'',
				id: '',
				psd: ''
			},
			teacher:{
				name: '',
				email: '',
				cover:'15/176/1570584130210.jpg',
				id: '',
				username: ''
			},
			rules: {
	          name: [
	            { validator: validateName, trigger: 'blur' }
	          ],
	          psd: [
	            { validator: validatePwd, trigger: 'blur' }
	          ],
	          email: [
	            { validator: validateEmail, trigger: 'blur' }
	          ]
	        },
		}
	},
	mounted(){
		if (this.getToken()) {
			this.getInfoParam();
			this.imgUrl = this.$ImgUrl + '/avatar/'
		}
	},
	methods:{
		//
		getInfoParam(){
			this.param.append('token',this.getToken());
			this.getInfo();
		},
		getInfo(){
			var that = this;
			var url = this.$baseUrl + '/info';
			this.axios.post(url,this.param)
			.then((res)=>{
				res = res.data;
				console.log(res);
				if (res.code == 200) {
					that.teacher.id = res.data.id;
					that.teacher.username = res.data.username;
					that.teacher.name = res.data.name;
					that.teacher.email = res.data.email;
					that.teacher.cover = res.data.avatar;
				}
			})
			.catch((e)=>{
				console.log(e);
			})
		},
	    //
	    cancle(){
	    	this.isEdit = false;
	    },
	    //
	    upInfo(){
	    	this.isEdit = true;
	    	this.upImg = '';
	    	this.ruleForm.name = this.teacher.name;
	    	this.ruleForm.email = this.teacher.email;
	    	this.ImgCover.token = this.getToken();
	    },
	    submit(){
	    	var A = this.isUpImg == true;
	    	var B = this.ruleForm.name == this.teacher.name && this.ruleForm.email == this.teacher.email && this.ruleForm.psd == '';
	    	console.log(A,B)
	    	if (A) {
	    		if (B) {
	    			this.$message.success('');
	    			this.isEdit = false;
	    			this.Upimg = '';
	    			this.getInfo();
	    		} else {
	    			this.UpDataInfo();
	    		}
	    		this.isUpImg = false;
	    	} else {
	    		if (B) {
	    			this.$message.warning('');
	    			return ;
	    		} else {
	    			this.UpDataInfo();
	    		}
	    	}
	    },
	    //
	    UpDataInfo(){
	    	var that = this;
	    	var param = new FormData();
	    	param.append('name',this.ruleForm.name);
	    	param.append('email',this.ruleForm.email);
	    	param.append('password',this.ruleForm.psd);
	    	param.append('token',this.getToken());
			var url = this.$baseUrl + '/uInfo/teacher';
			this.axios.post(url,param)
			.then((res)=>{
				res = res.data;
				console.log(res);
				if (res.code == 200) {
					that.$message.success('');
					that.isEdit = false;
					that.ruleForm.name ='';
					that.ruleForm.psd ='';
					that.ruleForm.email ='';
					that.getInfo();
				}
			})
			.catch((e)=>{
				console.log(e);
			})
	    },
	    //
		handleSuc(res, file, fileList){
			console.log(res);
			this.upImg = file.url;
			this.isUpImg = true;
		},
		handleErr(err, file, fileList){
			console.log(err);
			if (fileList.length == 0) {
				console.log('')
			}
			console.log(fileList);
		},
	    //token,token " "
    	getToken(){
    		var theToken = sessionStorage.getItem('token');
    		return theToken.slice(1,theToken.length-1);
    	}
	}
}
</script>

<style scoped lang="less">
@allwidth: 100/1386vw;
.selfInfo{
	width: 95%;
	margin: 0 auto;
	.pubHead{
		height: 50*@allwidth;
		padding-bottom: 10*@allwidth;
		border-bottom: 2px solid #3D7E8D;
		display: flex;
		align-items: flex-end;
		p{
			font-size: 25*@allwidth;
		}
		ul{
			margin-left: auto;
			display: flex;
			li{
				text-align: center;
				height: 35*@allwidth;
				line-height: 35*@allwidth;
				padding: 2*@allwidth 20*@allwidth;
				color: #fff;
				background: #3D7E8D;
				cursor: pointer;
				margin: 0 3*@allwidth;
				border-radius: 5px;
				font-size: 18*@allwidth;
			}
		}
		
	}
	.showInfo{
		margin-top: 30*@allwidth;
		border: 1px solid green;
		border-left: 10*@allwidth solid green;
		border-radius: 5*@allwidth;
		padding: 20*@allwidth 20*@allwidth;
		display: flex;
		align-items: center;
		img{
			width: 100*@allwidth;
			height: 100*@allwidth;
			border-radius: 50%;
		}
		ul{
			margin-left: 30*@allwidth;
			li{
				font-size: 18*@allwidth;
				margin: 20*@allwidth 0;
				display: flex;
				p{
					font-size: 16*@allwidth;
					margin-right: 30*@allwidth;
				}
			}
		}
	}
	.upInfo{
		width: 90%;
		margin: 60*@allwidth auto;
		img{
			background-color: #fbfdff;
		    border: 0.026667rem dashed #c0ccda;
		    border-radius: 0.16rem;
		    box-sizing: border-box;
		    width: 3.946667rem;
		    height: 3.946667rem;
		    line-height: 3.893333rem;
		    vertical-align: top;
		}
	}
}
</style>